import React from 'react'
import {KnowMe} from '../../portfolio'
import './knowme.css'

export default function Knowme() {
  return (
    <div>
        <section id='KnowMe' className='section projects'>
      <h2 className='section__title'>Know Me</h2>

     <div className='knowme'>
     <p className='knowme-section '>
            <span className='knowme-lyrics'>
            {KnowMe.lyrics} 
            </span>
            <span >
            {KnowMe.descriptionOne} 
            </span>
            <span className='knowme-personName'>
            {KnowMe.personName} 
            </span>
            <span>
            {KnowMe.descriptionTwo} 
            </span>
      </p>
     </div>
      </section>
    </div>
  )
}
